import PropTypes from "prop-types";
import { groupBy, maxBy, minBy } from "lodash";

const txBody = txReport => {
  const groupedTx = groupBy(txReport, "name");

  const row = Object.keys(groupedTx).map((name, i) => {
    const tx = groupedTx[name];
    console.log(maxBy(tx, tx.duration));
    return (
      <tr key={i}>
        <th>{name}</th>
        <td>{tx.length}</td>
        <td>Otto</td>
        <td>@mdo</td>
        <td>@mdo</td>
      </tr>
    );
  });
  return <tbody>{row}</tbody>;
};

const TransactionBreakdown = ({ txReport }) => {
  return (
    <div className="row" id="vu-section">
      <table class="table">
        <thead>
          <tr>
            <th scope="col">Transaction Type</th>
            <th scope="col">No. Execution</th>
            <th scope="col">Longest Execution (ms)</th>
            <th scope="col">Shortest Execution (ms)</th>
            <th scope="col">Average Execution (ms)</th>
          </tr>
        </thead>
        {txBody(txReport)}
      </table>
    </div>
  );
};

TransactionBreakdown.propTypes = {
  txReport: PropTypes.array
};

export default TransactionBreakdown;

